import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { generalActions } from "../../store/general-slice";
import { withStyles } from "@material-ui/core/styles";
import Switch from "@material-ui/core/Switch";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import React from "react";
import { devicesActions } from "../../store/sensors-slice";
import { useSelector } from "react-redux";

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
  },
  switchBase: {
    padding: 1,
    "&$checked": {
      transform: "translateX(16px)",
      color: theme.palette.common.white,
      "& + $track": {
        backgroundColor: "#198754",
        opacity: 1,
        border: "none",
      },
    },
    "&$focusVisible $thumb": {
      color: "#52d869",
      border: "6px solid #fff",
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: "darkorange",
    opacity: 1,
    transition: theme.transitions.create(["background-color", "border"]),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      checked={props.checked} // Change defaultChecked to checked
      onChange={props.onChange} // Pass onChange from props
      {...props}
    />
  );
});

const ToggleButton = (props) => {
  const [label, setLabel] = useState("static_load");
  const dispatch = useDispatch();

  const devices = useSelector((state) => state.devices["devices"]);
  const neededDevice = devices.find((device) => device.deviceID === props.deviceID);
  let toggle = neededDevice.toggle

  const [switchValue, setSwitchValue] = useState(
    // localStorage.getItem(`switchValue${props.deviceID}`) || "static_load"
    toggle
  );

  useEffect(()=>{
    setSwitchValue(toggle)
  },[toggle])

  const handleSwitchChange = (e) => {
    const newValue = e.target.checked ? "dynamic_load" : "static_load";
    setSwitchValue(newValue);
    // localStorage.setItem(`switchValue${props.deviceID}`, newValue);
    dispatch(
      devicesActions.setAllPageStatus({
        deviceID : props.deviceID,
        toggle : newValue
      })
    );

    dispatch(
      devicesActions.readToggle({
        deviceID: props.deviceID,
        value: newValue,
      })
    );
  };

  return (
    <>
      <div className="d-flex">
        <h4 className="flex-grow-1">Device {props.deviceID}</h4>
        <FormControlLabel
          control={
            <IOSSwitch
              id="toggle-two"
              checked={switchValue === "dynamic_load"} // Pass checked prop
              onChange={handleSwitchChange}
              disabled={props.status !== "idle"} //new
              // disabled={props.status !== "idle" && !props.status.includes("resume")}
            />
          }
          label={
            switchValue === "static_load"
              ? "Static Load"
              : switchValue === "dynamic_load"
              ? "Cyclic Load"
              : null
          }
          labelPlacement="start" // Align label to the start (left) of the container
          style={{
            justifyContent: "space-between",
            alignItems: "center",
            display: "flex",
          }} // Align items in a row
        />
      </div>
    </>
  );
};

export default ToggleButton;

