import "./App.css";
import StaticLoad from "./componants/static_load/static_load";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { devicesActions, fetchDeviceData } from "./store/sensors-slice";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Report from "./componants/Report/Report";
import Projects from "./componants/Report/Projects";


function App() {
  const dispatch = useDispatch();

  const deviceDataLoaded = useSelector((state) => state.devices.length > 0);

  useEffect(() => {
    // Dispatch the fetchDevices action when the component mounts
    dispatch(fetchDeviceData());
  }, [dispatch]);

  useEffect(() => {
    // Dispatch the GetallTopics action when device data is loaded
    if (deviceDataLoaded) {
      dispatch(devicesActions.GetallTopics());
    }
  }, [deviceDataLoaded, dispatch]);

  return (
    <Router>
    <Routes>
      <Route path="/" element={<StaticLoad />} />
      <Route path="/report/:projectId" element={<Report/>} />
      <Route path="/projects" element={<Projects />} />
    </Routes>
  </Router>
  );
}

export default App;
