import HookMqtt from "../Mqtt1";
import Information from "./Information";
import Adjust from "./adjust";
import NavBar from "./nav_bar";
import Status from "./status";
import ToggleButton from "./toggle_button";
import { useSelector } from "react-redux";

const Device = (props) => {
  let deviceData = props.device
  // console.log("device", deviceData);

  return (
    <div className="container">
      <div className="card mt-3 mb-3">
        <div className="card-header pt-3">
          <ToggleButton deviceID = {deviceData.deviceID} status={deviceData.status}/>
        </div>
        <div className="col card-body">
          <div className="row">
            <Adjust deviceID = {deviceData.deviceID}/>
            <Status deviceData = {deviceData}/>
          </div>
          <hr />
          <div className="row">
            <Information deviceID = {deviceData.deviceID}/>
          </div>
        </div>

      </div>
    </div>
  );
};

export default Device;
