//test 2
import React, { useEffect, useState } from "react";
import Information1 from "./Information1";
import { useSelector } from "react-redux";
import HookMqtt from "../Mqtt1";
import { useRef } from "react";
import { useDispatch } from "react-redux";
import { devicesActions } from "../../store/sensors-slice";
import { useNavigate } from "react-router-dom";

const Information = (props) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const ref3 = useRef();
  const [positiveDesignLoad, setPositiveDesignLoad] = useState(0);
  const [negativeDesignLoad, setNegativeDesignLoad] = useState(0);
  const [projectName, setProjectName] = useState("");
  const [isEditable, setIsEditable] = useState(false);

  const handleEditClick = () => {
    setIsEditable(!isEditable); // Toggle the editable state
  };

  function validateInput(value, setter) {
    if (value === "" || isNaN(value)) {
      setter(0);
    }
  }

  const deviceID = props.deviceID;
  const devices = useSelector((state) => state.devices["devices"]);
  const neededDevice = devices.find((device) => device.deviceID === deviceID);
  let toggle = neededDevice.toggle;
  let status = neededDevice.status;

  useEffect(() => {
    const positiveLoad = neededDevice.design_load_positive;
    const negativeLoad = neededDevice.design_load_negative;
    const projectNameee = neededDevice.project_name;

    if (positiveLoad !== null) {
      setPositiveDesignLoad(parseInt(positiveLoad));
    }
    if (negativeLoad !== null) {
      setNegativeDesignLoad(parseInt(negativeLoad));
    }
    if (projectNameee !== null) {
      setProjectName(projectNameee);
    }
  }, [neededDevice]);

  useEffect(() => {
    dispatch(
      devicesActions.setAllPageStatus({
        deviceID: props.deviceID,
        project_name: projectName,
        design_load_positive: positiveDesignLoad,
        design_load_negative: negativeDesignLoad,
      })
    );
  }, [positiveDesignLoad, negativeDesignLoad, projectName]);

  function updateTable() {
    sethalfLoadPositive(
      fullLoadPositive && (fullLoadPositive * 0.5).toFixed(2)
    );
    setdesignLoadPositive(positiveDesignLoad && positiveDesignLoad.toFixed(2));
    setfullLoadPositive(
      positiveDesignLoad && (positiveDesignLoad * 1.5).toFixed(2)
    );
    sethalfLoadNegative(
      fullLoadNegative && (fullLoadNegative * 0.5).toFixed(2)
    );
    setdesignLoadNegative(negativeDesignLoad && negativeDesignLoad.toFixed(2));
    setfullLoadNegative(
      negativeDesignLoad && (negativeDesignLoad * 1.5).toFixed(2)
    );
  }

  const [halfLoadPositive, sethalfLoadPositive] = useState(0);
  const [designLoadPositive, setdesignLoadPositive] = useState(0);
  const [fullLoadPositive, setfullLoadPositive] = useState(0);
  const [halfLoadNegative, sethalfLoadNegative] = useState(0);
  const [designLoadNegative, setdesignLoadNegative] = useState(0);
  const [fullLoadNegative, setfullLoadNegative] = useState(0);

  const [clicked, setClicked] = useState(false);

  useEffect(() => {
    updateTable();
  }, [
    positiveDesignLoad,
    negativeDesignLoad,
    fullLoadPositive,
    fullLoadNegative,
  ]);

  const HandlePositiveDesignLoad = (e) => {
    if (neededDevice.init == true) {
      setClicked(true);
    }
    const inputValue = e.target.value;
    // Check if the input value is a valid number (integer or floating point)
    if (/^-?\d*\.?\d*$/.test(inputValue)) {
      // Updated regular expression to allow for an optional "-"
      const parsedValue = inputValue === "" ? "" : parseFloat(inputValue);
      validateInput(parsedValue, setPositiveDesignLoad);
      setPositiveDesignLoad(parsedValue);
      updateTable();
    }
  };

  const HandleNegativeDesignLoad = (e) => {
    setClicked(true);
    const inputValue = e.target.value;
    // Check if the input value is a valid integer (including negative numbers)
    if (/^-?\d*\.?\d*$/.test(inputValue)) {
      const parsedValue = inputValue === "" ? "" : parseFloat(inputValue);
      validateInput(parsedValue, setNegativeDesignLoad);
      setNegativeDesignLoad(parsedValue);
      updateTable();
    }
  };

  const HandleProjectName = (e) => {
    const inputValue = e.target.value;
    setProjectName(inputValue);
  };

  const handleNewProjectBtn = () => {
    setPositiveDesignLoad(0);
    setNegativeDesignLoad(0);
    setProjectName("");
    dispatch(
      devicesActions.setAllPageStatus({
        deviceID: props.deviceID,
        project_name: "",
        design_load_positive: 0,
        design_load_negative: 0,
        current_index: 0,
      })
    );
    console.log("Cancel Resume");
    ref3.current.mqttPub({
      topic: `device${deviceID}/resume_cancel`,
      qos: 1,
      payload: "cancel",
    });
    // localStorage.setItem(`test_index_wanted_device(${props.deviceID})`,0)
    // dispatch(
    //   devicesActions.setAllPageStatus({
    //     deviceID : deviceID,
    //     current_index : 0,
    //   }))
  };

  return (
    <>
      <HookMqtt ref={ref3} topics={[]} />
      <div className="row">
        <div className="col">
          <div className="row">
            <div className="col-6 col-md-5 col-lg-4 mt-3">
              <h4>Project Information</h4>
            </div>

            <div className="col-6 col-md-7 col-lg-8 d-flex align-items-center">
              <div className="form-floating me-3 flex-grow-1">
                {/* <input
                  type="text"
                  className="form-control"
                  id="floatingInput"
                  placeholder="Project 1"
                  onChange={HandleProjectName}
                  value={projectName}
                /> */}
                <select
                  className="form-control"
                  id="floatingInput"
                  value={projectName}
                  onChange={HandleProjectName}
                >
                  <option value="">{projectName}</option>
                  {/* <option value="">Select a project</option>
                  <option value="Project 1">Project 1</option>
                  <option value="Project 2">Project 2</option>
                  <option value="Project 3">Project 3</option> */}
                </select>
                <label htmlFor="floatingInput">Project Name</label>
              </div>
              <button
                type="button"
                className="btn btn-warning btn-lg me-2"
                id="NewProject1"
                disabled={status !== "idle"}
                onClick={handleNewProjectBtn}
              >
                +
              </button>
              <button
                type="button"
                className="btn btn-warning btn-lg me-2"
                id="NewProject2"
                disabled={status !== "idle"}
                onClick={() => navigate("/projects")}
              >
                Details
              </button>
              <button
                type="button"
                className="btn btn-warning btn-lg"
                id="NewPr"
                onClick={handleEditClick}
              >
                {isEditable ? "Save" : "Edit"}{" "}
                {/* Button toggles between Edit and Save */}
              </button>
            </div>
          </div>

          <div className="row">
            <div className="table-responsive">
              <table
                className="table text-center table-bordered align-middle"
                style={{ tableLayout: "fixed" }}
              >
                <thead className="table-dark">
                  <tr>
                    <th></th>
                    <th>
                      <h5>Design load</h5>
                    </th>
                    <th>
                      <h5>Half load</h5>
                    </th>
                    <th>
                      <h5>Design Load</h5>
                    </th>
                    <th>
                      <h5>Full Load</h5>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <th className="table-dark">
                      <h5>Positive</h5>
                    </th>
                    <td>
                      <input
                        type="number"
                        className="form-control text-center align-middle"
                        id="positiveDesignLoad"
                        placeholder="Positive Design load"
                        value={positiveDesignLoad}
                        min="-9999"
                        max="9999"
                        onChange={HandlePositiveDesignLoad}
                        disabled={!isEditable} // Disable the field if not in edit mode
                      />
                    </td>
                    <td id="halfLoadPositive">{halfLoadPositive}</td>
                    <td id="designLoadPositive">{designLoadPositive}</td>
                    <td id="fullLoadPositive">{fullLoadPositive}</td>
                  </tr>
                  <tr>
                    <th className="table-dark">
                      <h5>Negative</h5>
                    </th>
                    <td>
                      <input
                        type="number"
                        className="form-control text-center align-middle"
                        id="negativeDesignLoad"
                        placeholder="Negative Design load"
                        value={negativeDesignLoad}
                        min="-9999"
                        max="9999"
                        onChange={HandleNegativeDesignLoad}
                        disabled={!isEditable} // Disable the field if not in edit mode
                      />
                    </td>
                    <td id="halfLoadNegative">{halfLoadNegative}</td>
                    <td id="designLoadNegative">{designLoadNegative}</td>
                    <td id="fullLoadNegative">{fullLoadNegative}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

      {
        // localStorage.getItem(`switchValue${props.deviceID}`) === "dynamic_load" ?
        toggle === "dynamic_load" ? (
          <Information1
            designLoadPositive={designLoadPositive}
            designLoadNegative={designLoadNegative}
            fullLoadPositive={fullLoadPositive}
            fullLoadNegative={fullLoadNegative}
            neededDevice={neededDevice}
            clicked={clicked}
            isEditable = {isEditable}
          />
        ) : null
      }
    </>
  );
};

export default Information;
